.header-sect {
  margin: 2rem 0;
}

.signup-col {
}

.chars-col {
  background: url('../../assets/illustration.svg');
  background-size: 85%;
  background-repeat: no-repeat;
  background-position: center bottom; 
}


@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 50px;
  }
}

.mobile-col {
  background: url('../../assets/phone.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;

  animation: MoveUpDown 0s linear infinite;
    position: absolute;
    left: 0;
    bottom: 0;
}

@media screen 
  and (max-device-width: 1200px){ 
    .no-title {
      display: none;
    }
    .chars-col{
      left: 25px;
      background-position: center right;
      box-shadow: 0px 0px 25px -5px #333;
      border-radius: 15px;
    }
    .mobile-col {
      display: none;
    }
}

@media screen 
  and (max-device-width: 800px){ 
    .chars-col{
      display: none;
    }
      
}

